export const initialState = {
    user: null,
    playlist: [],
    playing: false,
    items: null,
    // token:'BQAEAnweGXlQUg8odruju5yxRdIzyLtxCcMiDiQ6sB6sLc7EcwMiGaf5kxjVM3tpQgUso90Ggkf9XGpND1IPk2R5dxfrUEH4QE9YCs4XT2Wv2pwvPudYT9wEQZD6I7Yqc6XVPeEZk_YdEzeyiQhwQDfRDVDyBzMsCmGI1sb2v4pzmyTgSOqX'
    }
const reducer = (state, action) => {
  console.log(action);
  switch (action.type) {
    case "SET_USER":
      return {
        ...state,
        user: action.user,
      };

    case "SET_PLAYING":
      return {
        ...state,
        playing: action.playing,
      };

    case "SET_ITEM":
      return {
        ...state,
        item: action.item,
      };

    case "SET_DISCOVER_WEEKLY":
      return {
        ...state,
        discover_weekly: action.discover_weekly,
      };

    case "SET_TOP_ARTISTS":
      return {
        ...state,
        top_artists: action.top_artists,
      };

    case "SET_TOKEN":
      return {
        ...state,
        token: action.token,
      };

    case "SET_SPOTIFY":
      return {
        ...state,
        spotify: action.spotify,
      };

    case "SET_PLAYLISTS":
      return {
        ...state,
        playlists: action.playlists,
      };
    default:
      return state;
  }
};


export default reducer;